@media (min-device-height: 1080px) {
  content {
    height: 80vh;
    width: 100%;
    /* or whatever is required */ }
  .container {
    background: white;
    width: 90vh;
    padding: 15px;
    text-align: center;
    position: absolute;
    top: 15%;
    left: 50%;
    z-index: 70;
    content: "";
    display: inline-block;
    overflow: auto; }
  .titleSection {
    display: inline;
    margin-bottom: 10px;
    color: #015367; }
  .label {
    color: #015367; }
  .titleResult {
    text-align: center;
    font-weight: bold;
    font-size: 120%;
    display: inline; }
  .result {
    display: inline;
    font-size: 100%;
    margin-bottom: 10px;
    color: #015367; }
  .list {
    overflow: auto !important;
    height: 45vh;
    color: #015367; }
  .menu {
    background: #60B9CE linear-gradient(#60B9CE, #04819E);
    /*font-family: "Helvetica neue", Helvetica Neue, Helvetica, Arial, sans-serif;*/
    text-shadow: 0 -1px 0 #354C8C; }
  .menu:hover,
  .menu:focus {
    background: #04819E linear-gradient(#04819E, #206876); } }

